.navbar {
  height: 60px;
}
.wrappern {
  display: flex;
  padding: 10px 20px;
  justify-content: space-between;
}

.left,
.center,
.right {
  flex: 1;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.left {
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 450px) {
  .left {
    display: none;
  }
}
.right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2px;
  position: relative;
}
.span {
  font-size: 14px;
  cursor: pointer;
}
.inputContainer {
  border: 1px solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 25px;
  padding: 5px;
}
.input {
  border: none;
}
.buttonn {
  margin-right: 25px;
  cursor: pointer;
  padding: 5px;
  font-size: 12px;
}
/* .center {
  display: flex;
  align-items: center;
  justify-content: flex-start;
} */
.logo {
  font-size: 22px;
  text-align: center;
  cursor: pointer;
}
.username {
  font-size: 18px;
  margin-right: 20px;
}
.logout {
  position: absolute;
  right: -10px;
  top: 40px;
  font-size: 12px;
  font-weight: 100;
  letter-spacing: 1px;
  color: gray;
  cursor: pointer;
}
@media only screen and (max-width: 991px) {
  .logout {
    top: 30px;
  }
}
